@font-face {
  src: local('Kooka-Regular'), url(/to-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

* {
  background-color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Arial, Helvetica, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  src: local('Kooka-Regular'), url(/to-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

:root {
  /* root colors, these are all of the flowers in the canva logo i made */
  --dark-green: #5b846d;
  --orange: #ef7525;
  --lilac: #cb6ce6;
  --red: #ef1963;
  --purple: #8c52ff;
  --blue: #80bef8;
  --lime: #c9e265;
  --yellow: #ffde59;
}

.App {
  text-align: center;
  background-color: black;
}

